import React from 'react'
import './SideDrawer.css'
import {NavLink} from 'react-router-dom'
import {ReactComponent as Logo} from '../Nav_Bar/logo_black_normal.svg'

class SideDrawer extends React.Component{

	render(){
		return (
		<div>
		<div className="backdrop" onClick={this.props.click}></div>
		<div className="sideDrawer">
		<div className="mobLogo">
		
		</div>
		<div>
		<div class="mob-menu">
		<ul>
		<li><NavLink to="/our-product" exact activeClassName="active_menu_item">Our Products</NavLink></li>
		<li><NavLink to="/about" exact activeClassName="active_menu_item">About Us</NavLink></li>
		<li><NavLink to="/case-studies" exact activeClassName="active_menu_item">Case Studies</NavLink></li>
		<li><NavLink to="/our-partners" exact activeClassName="active_menu_item">Our Partners</NavLink></li>
		<li><NavLink to="/our-support" exact activeClassName="active_menu_item">Our Support</NavLink></li>
		</ul>
		</div>
		</div>
		</div>
		</div>
		)
	}

};
export default SideDrawer