import React from 'react'
import './Button.css'
import {NavLink} from 'react-router-dom'

class NavButton extends React.Component{
	render(){
		return (
		<button className="NavButton">
		<NavLink to={this.props.href}>{this.props.text}</NavLink>
		</button>
		
		)
	}
	
}

export default NavButton