import React from 'react'
import './hamburger.css'


class Hamburger extends React.Component{
	render(){
		return (
		<button className="toggle-button" onClick={this.props.click}>
	<div className="toggle-button__line toggle-button__line1"/>
	<div className="toggle-button__line toggle-button__line2"/>
	<div className="toggle-button__line toggle-button__line3"/>
	</button>
		)
	}

};
 export default Hamburger;