import React from "react"
import {NavLink} from 'react-router-dom'
import {ReactComponent as Logo} from './logo_black_normal.svg'
import Hamburger from '../UI/hamburger/Hamburger'
import NavButton from '../UI/Buttons/NavButton'
import "./navbar.css"
import { faShoppingCart } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"


const Nav = (props)=> {
  return (
  <div className='topNav'>
	<div className='logo item'>
	<NavLink to="/" exact activeClassName="active_menu_item"><Logo/></NavLink>
	</div>
	<div className="menu item">
	<div onClick={props.close} className="menu__links">
    <NavLink to="/our-product" exact activeClassName="active_menu_item">RFID</NavLink>
	<NavLink to="/about" exact activeClassName="active_menu_item">NFC</NavLink>
	<NavLink to="/case-studies" exact activeClassName="active_menu_item">BLE</NavLink>
	<NavLink to="/our-partners" exact activeClassName="active_menu_item">LoRa</NavLink>
	<NavLink to="/our-support" exact activeClassName="active_menu_item">NB-IoT</NavLink>
	</div>
	</div>
	<div className="toggle-div item">
	<div >
	<FontAwesomeIcon className="icon" icon={faShoppingCart} />
	</div>
	<div>
	<Hamburger click={props.hamBurgerClickHandler} />
	</div>
	</div>
	</div>
  );
}
export default Nav;
