import React from "react";
import Nav from "./Components/Nav_Bar/Nav_Bar"
import SideDrawer from "./Components/SideDrawer/SideDrawer"
import Home from "./Components/Home/Home"
import About from "./Components/About/About"
import Case_Studies from "./Components/Case_Studies/Case_Studies"
import Our_Partners from "./Components/Our_Partners/Our_Partners"
import Support from "./Components/Support/Support"
import Footer from "./Components/Footer/Footer"
import Error from "./Components/Error/Error"
import {Route,Switch} from 'react-router-dom'
class App extends React.Component{
	state={isSideDrawerOpen:false}
	hamBurgerClickHandler = ()=>{
		this.setState((prevState)=>{
			return {isSideDrawerOpen:!prevState.isSideDrawerOpen}
		})
	
		
	}
	backDropClickHandler= ()=>{
		this.setState({isSideDrawerOpen:false})
	}
	render(){
		let sideDrawer;
		if(this.state.isSideDrawerOpen)
			sideDrawer=<SideDrawer text="ok" click={this.backDropClickHandler}/>
		 return (
    <div className="App">
      <Nav close={this.backDropClickHandler} hamBurgerClickHandler={this.hamBurgerClickHandler} />
	  {sideDrawer}
	  <Switch>
	  <Route path="/" component={Home} exact/>
	  <Route path="/about" component={About}/>
	  <Route path="/case-studies" component={Case_Studies}/>
	  <Route path="/our-partners" component={Our_Partners}/>
	  <Route path="/support" component={Support}/>
	  <Route component={Error}/>
	  </Switch>
	  <Footer/>
    </div>
  );
	}
}

export default App;
